.auth {
  display: flex;
  justify-content: center;
  padding-top: 100px;
  flex-grow: 1;
  width: 100%;

  >div {
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
  }
}

.auth-form__title {
  text-align: center;
  color: $color11;
  font-size: 36px;
  font-family: "Montserrat-Medium";
  font-weight: 500;
  margin-bottom: 40px;
}

.auth-form {
  margin: 0 auto;
  background: $color10;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
  padding: 30px 20px;
  border-radius: 5px;
  width: 370px;
  min-height: 445px;
  max-width: 400px;

  button {
    margin: 10px auto;
    margin-bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 14px 30px;
    height: 49px;
    font-size: 17px;
    font-weight: 400;
    width: 50%
  }

  .input:nth-child(2) {
    margin-bottom: 25px;
  }

  .input:nth-child(3) {
    margin-bottom: 15px;
  }
}

.error-message {
  margin-top: 15px;
  color: $color5;
  text-align: center;
}

.success-message {
  margin-top: 15px;
  color: $color8;
}

@media (max-width: 768px) {
  .auth-form {
    width: 100%;

    button {
      width: 80%
    }
  }
}