.register {
  display: flex;
  justify-content: center;
  padding-top: 100px;
  flex-grow: 1;
  width: 100%;

  >div {
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
  }
}

.register-form__title {
  text-align: center;
  color: $color11;
  font-size: 36px;
  font-family: "Montserrat-Medium";
  font-weight: 500;
  margin-bottom: 40px;
}

.register-form {
  margin: 0 auto;
  background: $color10;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
  padding: 30px 20px;
  border-radius: 5px;
  width: 370px;
  height: 637px;

  img {
    display: inline-block;
    bottom: 64px;
    left: 296px;
  }

  span {
    position: relative;
    top: -30px;

    &:nth-child(4) {
      right: 25px;
    }
  }


  button {
    margin: 50px auto;
    margin-bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 14px 30px;
    height: 49px;
    font-size: 17px;
    font-weight: 400;
    width: 70%;

    img {
      bottom: 64px;
      left: 212px;
    }
  }

  input {
    &:nth-child(2) {
      margin-bottom: 35px;
    }

    &:nth-child(3) {
      margin-bottom: 35px !important;
      height: 36px;
    }

  }
}

.error-message {
  margin-top: 15px;
  color: $color5;
}

.success-message {
  margin-top: 15px;
  color: $color8;
}


@media (max-width: 768px) {
  .register-form {
    width: 100%;

    button {
      width: 80%
    }

    img {
      left: 215px;
    }
  }

}